import Vue from 'vue'
import Router from 'vue-router'
import StoreCenter from '@/views/StoreCenter.vue'
import Info from '@/views/Info.vue'
import Scan from '@/views/Scan.vue'
import Performance from '@/views/Performance.vue'
import Wallet from '@/views/Wallet.vue'
import Exchangedetail from '@/views/Exchangedetail.vue'
import Credits from '@/views/Credits.vue'
import Orderdetail from '@/views/Orderdetail'
import Rank from '@/views/Rank.vue'
import Warehousing from '@/views/Warehousing.vue'
import Activation from '@/views/Activation.vue'
import Myqresult from '@/views/Myqresult.vue'

Vue.use(Router)

import VueWechatTitle from 'vue-wechat-title'
Vue.use(VueWechatTitle)

const routes = [{
	path: '/retailer/scan',
	component: Scan,
	name: 'Scan',
	meta: {
		title: '扫码入库'
	}
}, {
	path: '/retailer/index',
	component: StoreCenter,
	name: 'StoreCenter',
	meta: {
		title: '门店中心'
	}
}, {
	path: '/retailer/info',
	component: Info,
	name: 'Info',
	meta: {
		title: '门店中心'
	}
}, {
	path: '/retailer/performance',
	component: Performance,
	name: 'Performance',
	meta: {
		title: '业绩排行榜'
	}
}, {
	path: '/retailer/wallet',
	component: Wallet,
	name: 'Wallet',
	meta: {
		title: '我的钱包'
	}
}, {
	path: '/retailer/exchangedetail',
	component: Exchangedetail,
	name: 'Exchangedetail',
	meta: {
		title: '账单明细'
	}
}, {
	path: '/retailer/credits',
	component: Credits,
	name: 'Credits',
	meta: {
		title: '我的积分'
	}
}, {
	path: '/retailer/orderdetail',
	component: Orderdetail,
	name: 'Orderdetail',
	meta: {
		title: '订单列表'
	}
}, {
	path: '/retailer/rank',
	component: Rank,
	name: 'Rank',
	meta: {
		title: '业绩排行榜'
	}
}, {
	path: '/retailer/warehousing',
	component: Warehousing,
	name: 'Warehousing',
	meta: {
		title: '扫码入库'
	}
}, {
	path: '/retailer/activation',
	component: Activation,
	name: 'Activation',
	meta: {
		title: '零售户激活'
	}	
}, {
	path: '/retailer/myqresult',
	component: Myqresult,
	name: 'Myqresult',
	meta: {
		title: '门店中心'
	}
}]

const scrollBehavior = (to, from, savedPosition) => {
    if (savedPosition) {
        return savedPosition
    } else {
        return { x: 0, y: 0 }
    }
}

const router = new Router({
	mode: 'history',
	routes,
	scrollBehavior
})

export default router



